.button
  box-sizing: border-box
  width: auto
  height: auto
  border-radius: 20px
  border: 2px solid #000
  background: #FFF
  padding: 0.57% 4.549%
  display: flex
  justify-content: center
  align-items: center
.button:hover
  border: 2px solid #0047FF
.button:focus
  background: #0047FF
  border: 2px solid #0047FF
  .button__text
    color: white
  &__text
    color: #000
    font-family: Inter, sans-serif
    font-size: 24px
    font-style: normal
    font-weight: 400
    line-height: normal

.active
  display: none

@media screen and (max-width: 1600px)
  .button__text
    font-size: 22px
@media screen and (max-width: 1300px)
  .button__text
    font-size: 20px
@media screen and (max-width: 1000px)
  .button
    border: 1px solid #000
  .button__text
    font-size: 18px
  .button:hover
    border: 1px solid #0047FF
  .button:focus
    background: #0047FF
    border: 1px solid #0047FF
@media screen and (max-width: 900px)
  .button
    padding: 0.57% 3.549%
  .button__text
    font-size: 17px
@media screen and (max-width: 700px)
  .button__text
    font-size: 15px
@media screen and (max-width: 700px)
  .button
    padding: 0.57% 3%
  .button__text
    font-size: 14px
@media screen and (max-width: 550px)
  .button__text
    font-size: 12px
@media screen and (max-width: 450px)
  .button
    padding: 0.57% 2.8%
  .button__text
    font-size: 11px
@media screen and (max-width: 400px)
  .button
    padding: 0.57% 2.6%
  .button__text
    font-size: 10px