.section__order
  display: flex
  flex-direction: row
  justify-content: center
  &__wrap
    overflow: hidden
    margin-top: 9%
    display: flex
    flex-direction: column
    margin-left: 4.2%
    margin-right: 4.2%
  &__headerWrap
    display: flex
    flex-direction: column
    width: 100%
  &__headerText
    color: #000
    text-align: right
    font-family: Inter, sans-serif
    font-size: 40px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__lineImg
    margin-top: 3.25%
    margin-bottom: 7.2%
  &__elem1
    display: flex
    position: relative
    align-items: center
    justify-content: flex-start
    flex-direction: row
    border-radius: 20px
    background: #0047FF
    width: 30%
    min-width: 0
    min-height: 0
    flex-shrink: 0
  &__elem1Text
    position: absolute
    display: flex
    left: -37%
    flex-direction: column
    transform: rotate(90deg)
    color: #FFF
    text-align: center
    font-family: Inter, sans-serif
    font-size: 46px
    font-style: normal
    font-weight: 600
    line-height: normal
  &__elem1TextWrap
    display: flex
    justify-content: flex-start
  &__elem2
    display: flex
    flex-direction: column
    width: 20%
    text-align: center
    margin-left: 5%
    margin-right: 5%
  &__elem3
    width: 30%
    display: flex
    flex-direction: row
  &__formFields
    width: 100%
    padding-right: 7%
    display: flex
    flex-direction: column
  &__elem2Text1
    margin-top: 7.1%
    color: #2F2F2F
    text-align: center
    font-family: Inter, sans-serif
    font-size: 24px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__elem2Text2
    color: #000
    text-align: center
    font-family: Inter, sans-serif
    font-size: 24px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__field
    display: flex
    align-items: center
    justify-content: center
    border-radius: 10px
    background: #FFF
    width: 82%
    height: 50px
    flex-shrink: 0
  &__fieldText:nth-child(1n)
    margin-top: 10%
  &__fieldText
    display: flex
    justify-content: flex-end
    margin-bottom: 4%
    color: #FFF
    text-align: center
    font-family: Inter, sans-serif
    font-size: 20px
    font-style: normal
    font-weight: 700
    line-height: normal
  &__checkbox
    display: block
    height: 10px
    width: 10px
    border-radius: 20px
    color: black
  &__termsBlock
    display: flex
    justify-content: flex-end
    padding-left: 20%
    flex-direction: row
  &__buttonWrap
    width: 100%
    display: flex
    justify-content: flex-end
  &__button
    margin-top: 8%
    display: flex
    align-items: center
    justify-content: center
    border-radius: 30px
    border: 1px solid #989595
    background: #525157
    width: 150px
    height: 40px
    flex-shrink: 0
  &__button:hover
    opacity: 0.8
  &__buttonText
    color: #FFF
    text-align: center
    font-family: Inter, sans-serif
    font-size: 16px
    font-style: normal
    font-weight: 700
    line-height: normal
  &__text
    display: flex
    justify-content: flex-end
    color: #d2d0d0
    text-align: center
    font-family: Inter, sans-serif
    font-size: 16px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__text:hover
    color: white
  &__textDefault
    margin-top: 10%
    color: #ffffff
    text-align: right
    font-family: Inter, sans-serif
    font-size: 16px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__buttonTerms
    margin-top: 10%
    margin-left: 15%
  &__innerInput
    color: #1f1f1f
    text-align: center
    font-family: Inter, sans-serif
    font-size: 24px
    font-style: normal
    font-weight: 800
    line-height: normal
  &__cardImg
    background: url(../../assets/woman_order.png) no-repeat
    width: 341px
    height: 100%
    flex-shrink: 0
    display: flex
    flex-direction: column
    position: relative
    overflow: hidden
  &__cardFilter
    position: absolute
    background: radial-gradient(100% 50.00% at 50% 50.00%, #000 0%, rgba(0, 0, 0, 0.00) 100%)
    width: 100%
    height: 50%
    margin-top: 71%
    z-index: 2
  &__socialsGroup
    margin-left: 6%
    display: flex
    justify-content: space-between
    flex-direction: column
  &__socialsWU, &__socialsVK, &__socialsTG, &__socialsPhone
    display: flex
    align-items: center
    justify-content: center
    border-radius: 20px
    width: 164%
    height: 173%
  &__socialsWU
    background: #698F72
  &__socialsWU:hover
    opacity: 0.9
  &__socialsVK
    background: #0047FF
  &__socialsVK:hover
    opacity: 0.9
  &__socialsTG
    background: #658BAD
  &__socialsTG:hover
    opacity: 0.9
  &__socialsPhone
    background: #525157
  &__socialsPhone:hover
    opacity: 0.9
  &__ImgText1
    z-index: 4
    margin-left: 5%
    margin-top: 90%
    color: #FFF
    font-family: Inter, sans-serif
    font-size: 32px
    font-style: normal
    font-weight: 600
    line-height: 40px
  &__ImgText2
    z-index: 4
    margin-left: 5%
    margin-top: 3%
    color: #FFF
    font-family: Inter, sans-serif
    font-size: 20px
    font-style: normal
    font-weight: 600
    line-height: 30px

.line__order
  margin-top: 7.1%
  margin-bottom: 7.1%

.black__logo__order
  min-width: 0
  min-height: 0

.black__logo__orderWrap
  display: flex
  align-items: center
  justify-content: center

@media screen and (max-width: 1600px)
  .section__order__headerText
    font-size: 36px
@media screen and (max-width: 1300px)
  .section__order__headerText
    font-size: 32px
@media screen and (max-width: 1000px)
  .section__order__headerText
    font-size: 28px
@media screen and (max-width: 800px)
  .section__order__headerText
    font-size: 24px
@media screen and (max-width: 650px)
  .section__order__headerText
    font-size: 20px
@media screen and (max-width: 550px)
  .section__order__headerText
    font-size: 16px
@media screen and (max-width: 450px)
  .section__order__headerText
    font-size: 14px
