@import "src/styles-global/mixins"

.section
  &__review__wrap
    display: flex
    flex-direction: column
    padding-left: 4.2%
    padding-right: 4.2%
    margin-bottom: 4.2%
    margin-top: 4.2%
  &__review__header
    color: #000
    text-align: right
    font-family: Inter, sans-serif
    font-size: 40px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__review__lineImg
    width: 100%
    margin-top: 2.3%
    margin-bottom: 0.8%
  &__review__gisImg
    display: flex
    justify-content: flex-start
    width: 13%
  &__review__button
    display: flex
    justify-content: center
    align-items: center
    padding: 0.683% 3.412%
    border-radius: 40px
    background: #525157
  &__review__buttonText
    color: #FFF
    font-family: Inter, sans-serif
    font-size: 24px
    font-style: normal
    font-weight: 600
    line-height: normal
  &__review__buttonWrap
    margin-top: 4%
    width: 100%
    display: flex
    align-items: center
    justify-content: center

  @media screen and (max-width: 1600px)
    &__review__buttonText
      font-size: 20px
  @media screen and (max-width: 1300px)
    &__review__buttonText
      font-size: 18px
  @media screen and (max-width: 1100px)
    &__review__buttonText
      font-size: 16px
  @media screen and (max-width: 800px)
    &__review__buttonText
      font-size: 12px
  @media screen and (max-width: 700px)
    &__review__buttonText
      font-size: 10px
  @media screen and (max-width: 600px)
    &__review__buttonText
      font-size: 8px


@media screen and (max-width: 1600px)
  .section__review__header
    font-size: 36px
@media screen and (max-width: 1300px)
  .section__review__header
    font-size: 32px
@media screen and (max-width: 1000px)
  .section__review__header
    font-size: 28px
@media screen and (max-width: 800px)
  .section__review__header
    font-size: 24px

@media screen and (max-width: 650px)
  .section__review__header
    font-size: 20px
@media screen and (max-width: 550px)
  .section__review__header
    font-size: 16px
@media screen and (max-width: 450px)
  .section__review__header
    font-size: 14px