@import "src/styles-global/mixins"

.section__map
  &__wrap
    display: flex
    flex-direction: column
    justify-content: center
    padding-left: 4.2%
    padding-right: 4.2%
  &__heaaderWrap
    width: 100%
  &__header
    color: #000
    display: flex
    justify-content: flex-end
    font-family: Inter, sans-serif
    font-size: 40px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__lineImg
    margin-top: 2.1%
    width: 100%
  &__mapWrap
    margin-top: 6.5%
    margin-bottom: 6.5%
    display: flex
    align-items: center
    justify-content: center
  &__map
    width: 70%
    flex-shrink: 0
    background-color: #b0b0b0

@media screen and (max-width: 1600px)
  .section__map__header
    font-size: 36px
@media screen and (max-width: 1300px)
  .section__map__header
    font-size: 32px
@media screen and (max-width: 1000px)
  .section__map__header
    font-size: 28px
@media screen and (max-width: 800px)
  .section__map__header
    font-size: 24px
@media screen and (max-width: 650px)
  .section__map__header
    font-size: 20px
@media screen and (max-width: 550px)
  .section__map__header
    font-size: 16px
@media screen and (max-width: 450px)
  .section__map__header
    font-size: 14px
