@import "src/styles-global/mixins"

.header
  background: $colorBlackNav
  display: flex
  flex-direction: row
  align-items: center
  justify-content: space-around
  padding: 0 2%
  &__button__link
    display: flex
    flex-direction: row
    cursor: pointer
    align-items: center


  &__wrap
    z-index: 20
    width: 100%
    position: sticky
    position: -webkit-sticky
    top: 0
  &__logo__vv
    color: $colorWhite
    font-size: 40px
    font-family: Cormorant SC, sans-serif
    font-style: normal
    font-weight: 300
    padding-top: 1.5%
    padding-bottom: 1.5%
  &__icon__map
    margin-left: 10%
    margin-right: 5%
    width: 10.3%
    flex-shrink: 0
  &__img__line

  &__navText
    &__shop

    &__catalog, &__review, &__order

    &__shop, &__about, &__catalog, &__review, &__order
      color: #FFF
      font-size: 24px
      font-family: Inter, sans-serif
      font-style: normal
      font-weight: 200
    &__textShop
      margin-left: 0.521% !important
  &__button__phone

  &__button__city

  &__button__phone, &__button__city
    display: flex
    align-items: center
    justify-content: center
    padding: 0.365% 1.042%
    border-radius: 30px
    border: 1px solid $colorWhite
    background: $colorBlackNav

  &__buttonText__phone, &__buttonText__city
    color: $colorWhite
    font-size: 24px
    font-family: Inter, sans-serif
    font-style: normal
    line-height: normal
  &__buttonText__phone
      font-weight: 600
  &__buttonText__city
      font-weight: 300

