.section
  &__review__sliderWrap
    margin-top: 4%
    display: flex
    align-items: center
    justify-content: center
  &__review__SliderWrap
    width: 100%
    display: flex
    align-items: center
    justify-content: center
  &__review__slider
    display: flex
    flex-direction: row
    justify-content: space-between
    width: 80%
  &__review__sliderColumn2,
  &__review__sliderColumn1
    display: flex
    flex-direction: column
    justify-content: space-between
    gap: 30px
  &__review__sliderColumn1
    margin-right: 2%
  &__review__sliderColumn2
    margin-left: 2%