.section
  &__catalog
    display: flex
    flex-direction: column
    margin-top: 4.2%
    padding-left: 4.2%
    padding-right: 4.2%
  &__catalog__header
    color: #000
    font-family: Inter, sans-serif
    font-size: 40px
    font-style: normal
    font-weight: 400
    line-height: normal
    display: block
  &__catalog__line
    width: 100%
    margin-bottom: 2.275%
    margin-top: 2.275%
  &__catalog__buttonRow
    box-sizing: border-box
    width: auto
    height: auto
    padding-left: 3%
    padding-right: 3%
    display: flex
    flex-direction: row
    align-items: center
    justify-content: space-between
  &__catalog__buttonRedirect
    margin-left: 3%
    border-radius: 30px
    background: #3D4350
    padding: 0.8% 1.43%
  &__catalog__buttonRedirectText
    color: #FFF
    font-family: Inter, sans-serif
    font-size: 24px
    font-style: normal
    font-weight: 600
    line-height: normal
  &__catalog__textRight
    display: flex
    align-items: flex-end
    justify-content: flex-end
    color: #000
    font-family: Inter, sans-serif
    font-size: 32px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__catalog__buttonBlock
    margin-top: 4.6%
    display: flex
    flex-direction: row
    justify-content: space-between
    align-items: center
  &__catalog__imgSmallLine
    margin-top: 3%
    width: 100%

.fake__dynamic__slider
  margin-top: 2.843%
  width: 100%
  display: flex
  justify-content: space-between
  align-items: center
.fakeSlider__elem
  &__ender
    border-radius: 30px
    width: 13%
    height: 15rem
    background: #91949d
  &__inner
    border-radius: 30px
    width: 18%
    height: 20rem
    background: #91949d
  &__center
    border-radius: 30px
    width: 20%
    height: 30rem
    background: #91949d

.section__catalog__textBlock
  display: flex
  align-items: flex-end
  justify-content: flex-end
  flex-direction: column

