@import "src/styles-global/mixins"

.comment
  border-radius: 15px
  background: #0047FF
  display: flex
  flex-direction: column
  align-items: flex-start
  position: relative
  &__text
    width: 80%
    margin-left: 5%
    padding-top: 3.35%
    padding-bottom: 3.35%
    color: #FFF
    font-family: Inter, sans-serif
    font-size: 20px
    font-style: normal
    font-weight: 400
    line-height: normal
  &__starImgArray
    position: absolute
    display: flex
    flex-direction: row
    right: 2%
    bottom: 10%
.star
  min-width: 0
  min-height: 0

@media screen and (max-width: 800px)
  .comment__text
    font-size: 10px
    margin-bottom: 5%
  .comment__starImgArray
    bottom: 5%
    display: block

@media screen and (max-width: 550px)
  .comment__text
    margin-bottom: 10%
    margin-left: 7%
    padding-top: 5%

@media screen and (max-width: 340px)
  .comment__text
    margin-bottom: 30%

@media screen and (max-width: 800px)
  .star

    height: 0.8rem
