@import "src/styles-global/mixins"

.section__footer
  display: flex
  flex-direction: row
  justify-content: space-around
  align-items: center
  width: 70%
  &__wrap
    width: 100%
    height: 110px
    flex-shrink: 0
    background-color: #242526
    display: flex
    flex-direction: row
    justify-content: space-around
    align-items: center
  &__text
    color: $colorWhite
    font-family: Inter, sans-serif
    font-size: 20px
    font-style: normal
    font-weight: 400
    line-height: normal
    text-decoration-line: underline

@media screen and (max-width: 1300px)
  .section__footer
    width: 80%

@media screen and (max-width: 1100px)
  .section__footer__text
    font-size: 18px
  .section__footer__wrap
    height: 90px
@media screen and (max-width: 950px)
  .section__footer
    width: 90%

@media screen and (max-width: 850px)
  .section__footer__text
    font-size: 16px
  .section__footer__wrap
    height: 70px
@media screen and (max-width: 750px)
  .section__footer__text
    font-size: 14px
@media screen and (max-width: 650px)
  .section__footer__text
    font-size: 12px
  .section__footer__wrap
    height: 60px
@media screen and (max-width: 550px)
  .section__footer__text
    font-size: 10px
  .section__footer__wrap
    height: 50px
@media screen and (max-width: 450px)
  .section__footer__text
    font-size: 8px
  .section__footer__wrap
    height: 45px